self.importScripts('ext_lib.js')
// self.importScripts('update_manager.js')
import UpdateManager from './update_manager'
import { precacheAndRoute } from 'workbox-precaching'
precacheAndRoute(self.__WB_MANIFEST)

const toClient = new self.BroadcastChannel('swToClient')
const fromClient = new self.BroadcastChannel('clientToSw')

fromClient.onmessage = function(e) {
    if (e.data.action === 'skipWaiting' && self.skipWaiting) {
        self.skipWaiting()
    }
}
self.addEventListener('push', async function (event) {
    const data = event.data.json()
    const respMsgArr = await self.updateManager.processPartialUpdate(data)
    toClient.postMessage(respMsgArr)
})

